<template>
    <div class="workHandel" >
        <navigation-bar
          pageName="工单处理"
          @onLeftClick="goBack"/>
        <div class="box">
            <div class="area-box">
                <textarea name="" id="" placeholder="请填写处理情况..." v-model="workContent" maxlength="100" @input="filterContent"></textarea>
                <div class="num">{{workContent.length}}/100</div>
            </div>
            <div class="content">
                <!--<div class="visible-box">
                    <div class="title">业主是否可见</div>
                    <van-switch v-model="isVisiable" size="24px" active-color="#00d8d1" inactive-color="#FFFFFF"/>
                </div>-->
                <div class="photo-box">
                    <div class="title">
                        <div>上传图片</div>
                        <div class="limit">(最多4张)</div>
                    </div>
                    <div class="photo">
                        <add-image v-on:imageAddAction="showAddSheetControll" v-on:imageMapChange="upDateImageData" ref="addImageComponent"></add-image>
                    </div>
                </div>
                <div class="submit-box">
                    <div class="submit" @click="handle">完成</div>
                </div>
            </div>
        </div>

        <van-action-sheet
          v-model="showAddSheet"
          :actions="actions"
          cancel-text="取消"
          round
          close-on-click-action
          @select="addSelect"/>
    </div>
</template>

<script>
import { handleWork } from "../../services/internalWorkPlanService";
import { store } from "../../utils/store";
import AddImageVue from "../addImage/AddImage.vue";
import NavigationBar from "../title/NavigationBar";
export default {
  name: "workHandle",
  data() {
    return {
      isVisiable: false,

      workContent: "",
      workDetail: null,
      actions: [{ name: "拍照" }, { name: "从手机相册选择" }],
      imgList: [],
      showAddSheet: false,
      userInfo: null
    };
  },
  components: {
    "add-image": AddImageVue,
    NavigationBar
  },
  created() {
    this.workDetail = this.$route.params;
    this.userInfo = store.get("userInfo");
  },
  methods: {
    /**
     * 页面返回事件
     */
    goBack: function() {
      this.$router.back(-1);
    },

     /**
     * 子组件更新已选图片集合监听函数
     **/
    upDateImageData(newImageData){
      this.imgList = newImageData;
    },

    /**
     * addImage组件添加照片时的监听函数父组件用来选择哪种图片获取方式(拍照或相册上传)
     **/
    showAddSheetControll() {
      this.showAddSheet = !this.showAddSheet;
    },

    /**
     * 选择当前图片来源方式(拍照或本地上传)
     * @param
     * item      Object   当前选择的方式对象
     * index     Number   当前选择的索引
     **/
    addSelect(item, index) {
      this.getPictureData(index);
    },

    /**
     * 调用摄像头或者本地相册进行图片上传
     * @param
     **/
    getPictureData(selectIndex) {
      selectIndex?this.$refs.addImageComponent.getPicture():this.$refs.addImageComponent.useCamera();
    },

    /**
     * 禁止输入表情
     */
    filterContent: function() {
      this.workContent = this.workContent.replace(
        /[\uD83C|\uD83D|\uD83E][\uDC00-\uDFFF][\u200D|\uFE0F]|[\uD83C|\uD83D|\uD83E][\uDC00-\uDFFF]|[0-9|*|#]\uFE0F\u20E3|[0-9|#]\u20E3|[\u203C-\u3299]\uFE0F\u200D|[\u203C-\u3299]\uFE0F|[\u2122-\u2B55]|\u303D|[\A9|\AE]\u3030|\uA9|\uAE|\u3030/gi,
        ""
      );
    },

    /**
     * 完成点击事件
     */
    handle: function() {
      if (!this.hasCompleteParam()) {
        return;
      }
      var workHandelData = {
        complCode: this.workDetail.complCode,
        complCatCode: this.workDetail.complCatCode,
        content: this.workContent,
        isShow: this.isVisiable ? 1 : 0,
        picUrl: this.imgList,
        picCode: "", // 暂时不用
        areaCode: this.workDetail.areaCode,
        createdBy: this.userInfo.USER_CODE,
        userCode: this.userInfo.USER_CODE,
        userName: this.userInfo.USER_NAME,
        userNickName: this.userInfo.USER_NICK_NAME,
        phone: this.userInfo.PHONE
      }
      handleWork(workHandelData).then(
        res => {
          if (res.resultCode == "0") {
            this.$toast("处理成功");
            store.set('insideWorkDetailComplCode', this.workDetail.complCode);
            this.goBack();
          } else {
             this.$toast(res.msg);
          }
        },
        error => {
          this.$toast(error);
        }
      );
    },

    /**
     * 判断是否必选的参数填写完
     */
    hasCompleteParam: function() {
      if (!this.workContent) {
        this.$toast("请填写处理信息！");
        return false;
      }
      // if (this.imgList.length <= 0) {
      //   this.$toast("请上传处理图片！");
      //   return false;
      // }
      return true;
    }
  },
  computed: {}
};
</script>
