<template>
  <div>
    <ul class="image-list">
      <li
        v-for="(picUrl,picIndex) in selectPictures"
        v-bind:key="picIndex"
        v-bind:style="{width:imageWidthAndHeight+'px',height:imageWidthAndHeight+'px'}"
      >
        <van-icon
          v-if="isShowAdd"
          class="delete-picture"
          color="rgb(181, 181, 181)"
          @click="deletePicture(picIndex)"
          name="clear"
        />
        <img :src="picUrl" @click="preview(picIndex)" alt />
      </li>
      <li v-if="accountAddImage && isShowAdd">
        <div class="add-image" @click="addAction">
          <img class="cls-icon" src="../../assets/imgs/find/upload_img.png" alt="">
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import { ImagePreview, Toast } from "vant";
import config from "../../common/config";
import { store } from "../../utils/store";
export default {
  name: "AddImage",
  props: {
    maxImageCount: {
      type: Number,
      default: 4
    },
    selectPictures: {
      type: Array,
      default: function() {
        return []
      }
    },
    isShowAdd: {
      type: Boolean,
      default: true
    },
    imageType: {
      type: Number,
      default: -1,
    }
  },
  data() {
    return {
      // selectPictures: []
    };
  },
  mounted() {
    //image 宽度高度自适应
    let ulDom = document.querySelector(".image-list");
    this.imageWidthAndHeight = ulDom.offsetWidth / 4;
  },
  computed: {
    //超过最大数不显示添加
    accountAddImage() {
      return this.selectPictures.length < this.maxImageCount;
    },

    //相册选择图片最大数值
    maximumImagesCount() {
      return this.maxImageCount - this.selectPictures.length;
    }
  },

  watch: {
    selectPictures(newValue, oldName) {
      console.log(newValue)
      this.$emit("imageMapChange", this.selectPictures.slice(0));
    }
  },
  methods: {
    /**
     * 图片预览
     * @param
     * startPosition  Number   当前选中图片索引
     **/
    preview(startPosition) {
      ImagePreview({ images: this.selectPictures, startPosition });
    },

    /**
     * 图片添加行为
     **/
    addAction() {
      this.$emit("imageAddAction");
    },

    /**
     * 删除指定的图片
     * @param
     * picIndex     Number    当前要删除图片的索引
     **/
    deletePicture(picIndex) {
      this.selectPictures.splice(picIndex, 1);
    },

    /**
     * 选择当前图片来源方式(拍照或本地上传)
     * @param
     * item      Object   当前选择的方式对象
     * index     Number   当前选择的索引
     **/
    addSelect(item, index) {
      index ? this.getPicture() : this.useCamera();
    },

    /**
     * 调用摄像头插件进行拍照
     * @param
     **/
    useCamera() {
      this.$plugin.usePlugin("camera")(
        imageData => {
          this.uploadFile(imageData);
        },
        error => {
          Toast.fail(error);
        }
      );
    },

    /**
     * 上传图片数据
     * @param
     * fileURL  String  图片本地资源路径
     **/
    uploadFile(fileURL) {
      let loading = Toast.loading({
        duration: 0,
        mask: true,
        message: "加载中..."
      });
      let reqUrl = `${store.get(
        "custUrl.homeUrl"
      )}xwj-sys-media/media/upload/uploadFile`;
      var options = {};
      options.params = {
        file: "",
        source: "wyApp",
        business: "complPic"
      };
      if ( 1 == this.imageType) {
        options.params.business = "dicoveryPic";
      }
      options.headers = { contentType: "multipart/form-data" };
      this.$plugin.usePlugin("fileTransfer")(
        reqUrl,
        fileURL,
        result => {
          loading.clear();
          let resData = JSON.parse(result.response);
          if (resData.resultCode == '0') {
            this.selectPictures.push(resData.data);
          } else {
            Toast.fail(resData.msg);
          }
        },
        error => {
          loading.clear();
          Toast.fail(error);
        },
        options
      );
    },

    /**
     * 从本地相册进行上传
     **/
    getPicture() {
      this.$plugin.usePlugin("getPictures")(
        this.maximumImagesCount,
        images => {
          images.forEach(imageData => {
            this.uploadFile(imageData);
          });
        },
        error => {
          Toast.fail(error);
        }
      );
    }
  }
};
</script>

