.image-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  > li {
    position: relative;
    width: 25%;
    flex-shrink: 0;
    padding: 3px;
    box-sizing: border-box;
    .delete-picture {
      position: absolute;
      right: 5px;
      top: 5px;
    }
    > img {
      width: 100%;
      height: 100%;
    }
    .add-image {
      width: 85px;
      height: 85px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #fff;
      border: 1px solid #e4e4e4;
    }
    .cls-icon {
      width: 87px;
      height: 87px;
    }
  }
}
</style>
